
Template.__checkName("day_heading");
Template["day_heading"] = new Template("Template.day_heading", (function() {
  var view = this;
  return HTML.H2({
    "class": "posts-day-heading"
  }, Blaze.View("lookup:formatDate", function() {
    return Spacebars.mustache(view.lookup("formatDate"), view.lookup("date"), "dddd, MMMM Do YYYY");
  }));
}));
