
Template.__checkName("posts_daily");
Template["posts_daily"] = new Template("Template.posts_daily", (function() {
  var view = this;
  return HTML.DIV({
    "class": "posts-daily"
  }, "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("days"));
  }, function() {
    return [ "\n      ", Spacebars.include(view.lookupTemplate("before_day")), "\n      ", HTML.DIV({
      "class": "posts-day"
    }, "\n        ", Blaze._TemplateWith(function() {
      return {
        date: Spacebars.call(view.lookup("date"))
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("day_heading"));
    }), "\n        ", Blaze._TemplateWith(function() {
      return Spacebars.call(view.lookup("arguments"));
    }, function() {
      return Spacebars.include(view.lookupTemplate("posts_list_controller"));
    }), "\n      "), "\n      ", Spacebars.include(view.lookupTemplate("after_day")), "\n    " ];
  }), "\n    ", Blaze._TemplateWith(function() {
    return {
      handler: Spacebars.call(view.lookup("loadMoreHandler"))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("load_more_days"));
  }), "\n  ");
}));
